.back-arrow-icon {
    float: left;
    margin-top: 32px;
    margin-left: 80px;
    position: relative;
    width: 24px !important;
    height: 24px !important;
    color: #0032a0;
    cursor: pointer;
}

.back-arrow-active-content {
    position: relative;
    font-size: 14px;
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    color: #0032a0;
    margin-top: 32px;
    margin-left: 15px;
    line-height: 150%;
    cursor: pointer;
}

.client-profile-screen-container {
    margin-top: 48px;
    margin-left: 280px;
    text-align: left;
}

.profile-screen-main-title {
    font-family: Red Hat Display !important;
    font-weight: 900 !important;
    font-size: 31px !important;
    line-height: 150% !important;
    color: #292724;
}

.profile-screen-require-field-title {
    font-family: Lato !important;
    font-weight: 800 !important;
    font-size: 14px !important;
    line-height: 150% !important;
    color: #87189d;
    text-align: left;
    margin-top: 16px;
}

.profile-screen-sub-title {
    font-family: Red Hat Display !important;
    font-weight: 900 !important;
    font-size: 20px !important;
    line-height: 150% !important;
    color: #292724;
    margin-top: 48px;
    margin-bottom: 24px;
}

.profile-screen-cards {
    width: 233px;
    height: 133px;
    margin-right: 20px;
    margin-bottom: 24px;
    background: #ffffff;
    border: 1px solid #d9d9d9;
    box-sizing: border-box;
    border-radius: 8px;
    box-shadow: none !important;
    text-align: center;
    cursor: pointer;
}

.profile-screen-card-icon-container {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    margin: 24px auto 16px;
    background: #b9cfff;
}

.profile-screen-card-success-icon {
    width: 48px;
    height: 48px;
    margin: 24px auto 16px;
}

.profile-screen-card-title {
    font-family: Lato !important;
    font-weight: 700 !important;
    font-size: 14px !important;
    color: #001135 !important;
}

.profile-screen-save-data-button {
    width: 102px;
    height: 48px;
    border-radius: 4px;
    font-weight: 800 !important;
    font-family: Lato !important;
    font-size: 14px !important;
    text-transform: none !important;
    margin: 24px 0px !important;
    float: right;
}

.profile-screen-save-data-button.MuiButton-contained.Mui-disabled {
    background: #d9d9d9 !important;
    color: #ffffff !important;
}

.profile-screen-save-data-button.MuiButton-containedPrimary {
    background: #0032a0 !important;
    color: #ffffff !important;
}

.tourism-radio-modal-title {
    position: relative;
    font-weight: 900;
    font-size: 25px;
    line-height: 130%;
    color: #292724;
    font-family: 'Red Hat Display', sans-serif;
    letter-spacing: 0.02em;
}

.tourism-radio-modal-subtitle {
    position: relative;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: #292724;
    font-family: 'Red Hat Display', sans-serif;
    letter-spacing: 0.02em;
    margin-top: 40px;
    text-align: left;
}

.profile-success-modal-button {
    margin-top: 48px !important;
    width: 100%;
    height: 48px;
    text-transform: none !important;
    font-family: Lato !important;
    font-weight: 800 !important;
    font-size: 14px !important;
    line-height: 150% !important;
    background: #b9cfff !important;
    color: #001135 !important;
}

.profile-success-modal-disabled-button {
    margin-top: 48px !important;
    width: 100%;
    height: 48px;
    text-transform: none !important;
    font-family: Lato !important;
    font-weight: 800 !important;
    font-size: 14px !important;
    line-height: 150% !important;
    background: #fafafa !important;
    color: #d9d9d9 !important;
}

.profile-input-field {
    position: relative;
    width: 360px;
    height: 48px;
    border-radius: 4px;
    margin: 8px 0px 20px 0px !important;
}

.client-screen-title-container {
    display: flex;
    width: 760px;
    justify-content: space-between;
}

.client-screen-title-skip-button {
    width: 168px;
    height: 48px;
    border-radius: 4px;
    font-weight: 800 !important;
    font-family: Lato !important;
    font-size: 14px !important;
    text-transform: none !important;
    margin-right: 20% !important;
    background: transparent !important;
    color: #0032a0 !important;
}

.client-demand-screen-data-container {
    width: 740px;
    background: #ffffff;
    border: 1px solid #d9d9d9;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 24px;
}

.client-demand-screen-button-container {
    width: 740px;
    display: flex;
    justify-content: flex-end;
    margin-top: 48px;
    padding-bottom: 48px;
}

.client-demand-screen-count-increment-disable {
    min-width: 24px !important;
    height: 24px !important;
    border: 1.5px solid #d9d9d9 !important;
    padding: 0px !important;
}

.client-demand-screen-increment-icon {
    width: 12px !important;
    height: 12px !important;
    color: #666666;
}

.client-demand-screen-count-increment {
    min-width: 24px !important;
    height: 24px !important;
    border: 1.5px solid #666666 !important;
    padding: 0px !important;
}

.client-demand-screen-increment-icon-disable {
    width: 12px !important;
    height: 12px !important;
    color: #d9d9d9;
}

.client-demand-screen-count-value {
    position: relative;
    font-size: 16px;
    font-family: Lato !important;
    color: #666666;
    padding: 0px 12px;
}

.client-demand-screen-date-selector {
    width: 338px;
    height: 48px;
    background: #ffffff;
    font-size: 14px;
}

.client-event-requirement-needs {
    font-family: Red Hat Display !important;
    font-weight: 800 !important;
    font-size: 20px !important;
    line-height: 150% !important;
    text-align: left;
    margin-bottom: 12px;
}

.client-event-requirement-subtitle {
    position: relative;
    font-size: 16px;
    font-family: Lato;
    color: #666666;
    text-align: left;
    margin-bottom: 30px;
}
