.study-day-card-description {
    font-family: Lato !important;
    font-size: 14px !important;
    text-align: left !important;
    color: #666666 !important;
    overflow-wrap: break-word;
    line-height: 150%;
    min-height: 45px;
    margin-top: 5px;
}

.study-day-options-card {
    text-align: center;
    margin-bottom: 3px;
    font-family: Lato;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    width: 153px;
    background: #eec6f6;
    border-radius: 4px;
    padding: 4px 8px;
}

.study-day-options-card-config {
    font-family: Lato;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    width: 140px;
    height: 26px;
    background: #eec6f6;
    border-radius: 4px;
    padding: 4px 8px;
}

.study-day-selection {
    text-transform: none !important;
    width: 134px;
    height: 48px;
    justify-content: center;
    padding: 13.5px 27px;
}

.study-day-selection-button {
    margin-top: 32px;
}

.wrapper {
    display: grid;
    float: left;
    margin-top: 10px;
    grid-template-rows: 140px 140px 140px;
}

.wrapper-container {
    width: 500px;
    display: grid;
    float: left;
    margin-top: 10px;
    grid-template-columns: 165px 165px 165px;
}

.wrapper-button {
    display: grid;
    grid-template-rows: 20px;
}

